.WholeCard {
  color: black;
  background: #fafad2;
}
.BlueButton {
  color: white;
  background: #6b68ff;
  border-radius: 40px 40px;
}
.WhiteButton {
  color: #6b68ff;
  background: white;
  border-radius: 40px 40px;
  border: 1px solid #6b68ff;
}
.BlueBack {
  background: #6b68ff;
  height: 50%;
}
.ovalTextField .MuiOutlinedInput-root {
  border-radius: 40px 40px;
}
.Halfcircle {
    width: 100%;
    height: 100px;
    background: #6b68ff;
    clip-path: ellipse(50% 100% at 50% 100%);
}
.UnderHalfcircle {
  width: 100%;
  height: 50vh;
  background: #6b68ff;
  border-radius: 0 0 25% 25%;
}